import React, { useEffect, useState } from 'react';
import { useDate } from '../../customHooks/useDate';

import TodoCreation from "../TodoCreation/TodoCreation";
import TodoList from '../TodoList/TodoList';

import { HomePageHeader, HomePageWrapper, HeaderGreeting, HeaderTime, HomePageMain } from './HomePage.styled';

const HomePage = () => {
    const [userName, setUserName] = useState('');
    const {time, fullDate} = useDate();

    useEffect(() => {
        if(localStorage.getItem('userName')) {
            setUserName(localStorage.getItem('userName')) 
        }
    }, []);

    return <>
        <HomePageWrapper>
            <HomePageHeader>
                <HeaderGreeting>
                <span>
                    {
                        userName ? `Hello, ${userName}! ðŸ‘‹` : 'Hello! ðŸ‘‹'
                    }
                </span>
                </HeaderGreeting>
                <HeaderTime>
                    <span>{time}</span>
                    <span>{fullDate}</span>
                </HeaderTime>
            </HomePageHeader>
            <HomePageMain>
                 <TodoCreation />
                 <TodoList />
            </HomePageMain>
        </HomePageWrapper>
    </>
}

export default HomePage;