import React, { useEffect, useState } from 'react';

import Button from '../UI/Button/Button';
import Modal from '../Modal/Modal';

import { TodoListStyled, TodoListWrapperStyled, TodoListItemStyled, Index, Text } from './TodoList.styled';

const TodoList = () => {
    const [todoList, setTodoList] = useState(JSON.parse(localStorage.getItem('todoList')) || []);
    const [modalIsOpened, setModalIsOpened] = useState(false);

    useEffect(() => {
        if(localStorage.getItem('todoList')) {
            setTodoList(JSON.parse(localStorage.getItem('todoList')));
        } else {
            setTodoList([]);
        }
    }, [localStorage.getItem('todoList')]);

    const handleClearAllClick = () => {
        setModalIsOpened(true);
    }

    const closeModal = () => {
        setModalIsOpened(false);
    }

    const handleConfirm = () => {
        localStorage.removeItem('todoList');
        document.location.reload();
    }

    return <>
        <TodoListWrapperStyled>
           <TodoListStyled>
               {todoList.length > 0 ? <Button 
                    danger
                    text={todoList.length > 1 ? 'Clear all' : 'Clear'}
                    onClick={handleClearAllClick}
               /> : null}
               {todoList.map((item, index) => <TodoListItemStyled key={item.id}>
                   <Index>{index + 1}.</Index>
                    <Text>{item.text}</Text>
               </TodoListItemStyled>)}  
           </TodoListStyled>
        </TodoListWrapperStyled>
        <Modal 
            text='Are you sure?'
            title='If you confirm this action, todo list will be completely deleted'
            open={modalIsOpened}
            onClose={closeModal}
            closeBtn={false}
            onConfirm={handleConfirm}
        />
    </>;
}

export default TodoList;