import React, { useEffect } from "react";
import { Routes, Route, useNavigate } from "react-router-dom";
import IntroductionPage from "./components/Intro/IntroductionPage";

import HomePage from "./components/Home/HomePage";

const App = () => {
  const navigate = useNavigate();

  useEffect(() => {
    if(!localStorage.getItem('skippedIntro') && !localStorage.getItem('userName')) {
      navigate('/name_creation')
    }
  }, [])

  return <>
    <Routes>
      <Route path='/name_creation' element={ <IntroductionPage />}></Route>
      <Route path="/" element={<HomePage />} />
    </Routes>
  </>
}

export default App;